@font-face {
  font-weight: 300;
  font-family: Montserrat;
  src: url("Montserrat-Light.f3f20713.ttf");
}

@font-face {
  font-weight: 400;
  font-family: Montserrat;
  src: url("Montserrat-Regular.96758019.ttf");
}

@font-face {
  font-weight: 600;
  font-family: Montserrat;
  src: url("Montserrat-SemiBold.eefb3b53.ttf");
}

@font-face {
  font-weight: 700;
  font-family: Montserrat;
  src: url("Montserrat-Bold.b170a58f.ttf");
}

@font-face {
  font-weight: 900;
  font-family: Montserrat;
  src: url("Montserrat-Black.c157a94f.ttf");
}

*, :before, :after {
  box-sizing: border-box;
}

body, h1, h2, h3, h4, h5, h6, p, button, figure, blockquote, dl, dd, ul, li {
  margin: 0;
  padding: 0;
}

ul, ol {
  list-style: none;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

button {
  color: inherit;
  cursor: pointer;
  font-size: inherit;
  background: inherit;
  border: none;
}

img, picture {
  object-fit: cover;
  object-position: center;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *, :before, :after {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

.btn-bg-slide {
  z-index: 1;
  aspect-ratio: 3.5 / 1;
  justify-content: center;
  align-items: center;
  display: inline-flex;
  position: relative;
}

.btn-bg-slide:before {
  content: "";
  width: 0;
  height: 100%;
  z-index: -1;
  background: var(--primary-accent-color);
  transition: all .2s;
  display: inline-block;
  position: absolute;
  left: 0;
}

.btn-bg-slide:hover:before {
  width: 100% !important;
}

.encircled-icon {
  aspect-ratio: 1 / 1;
  border: .35rem solid var(--primary-accent-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0 .7rem;
  transition: margin .1s;
  display: flex;
}

.encircled-icon svg {
  width: 60%;
  transition: fill .3s;
}

.encircled-icon:hover {
  margin-bottom: 1rem;
}

.encircled-icon:hover svg {
  fill: var(--primary-accent-color);
}

.image-bg {
  position: relative;
}

.image-bg > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  position: absolute;
}

.section-header {
  padding: 5rem 0;
}

.main-header {
  letter-spacing: .1rem;
  font-variant: small-caps;
  text-transform: capitalize;
  padding-bottom: 1rem;
  font-size: 4rem;
}

.main-header-bold {
  font-weight: 700;
}

.main-header-light {
  font-weight: 300;
}

.sub-header {
  letter-spacing: .5rem;
  text-transform: uppercase;
  color: var(--primary-accent-color);
  font-size: 1.4rem;
  font-weight: 600;
}

.paragraph {
  text-align: justify;
  letter-spacing: .2rem;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 2;
}

.paragraph-light {
  color: #707070;
}

.paragraph-dark {
  color: #ebebeb;
}

.section-dash-h {
  width: 7.5rem;
}

.section-dash-v {
  height: 7.5rem;
}

.pagination {
  justify-content: center;
  align-items: center;
  font-size: 1.25rem;
  display: flex;
}

.pagination * {
  transition: all .3s ease-out;
}

.pagination .page {
  margin: 1rem;
  font-weight: 700;
}

.pagination .active {
  color: var(--primary-accent-color);
  font-size: 140%;
}

:root {
  --primary-accent-color: #fcae1e;
  --dark-bg-0: #111517;
}

.cepkoncept-about {
  width: 95%;
  box-shadow: .1rem .1rem var(--primary-accent-color), .5rem .5rem black;
  border: .4rem solid #000;
  margin: 15rem auto;
  position: relative;
}

.cepkoncept-about .image {
  display: none;
}

.cepkoncept-about .content {
  padding: 0 3rem;
}

.cepkoncept-about .content .crown {
  height: 7.5rem;
  border-left: .3rem solid var(--primary-accent-color);
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

.cepkoncept-about .content .about-header {
  text-align: center;
}

.cepkoncept-about .content .about-footer {
  justify-content: space-between;
  padding: 4rem 0;
  display: flex;
}

.cepkoncept-about .content .about-footer .about-socials {
  justify-content: center;
  align-items: center;
  display: flex;
}

.cepkoncept-about .content .about-footer .about-socials .encircled-icon {
  width: 3.25rem;
}

.cepkoncept-about .content .about-footer .about-socials > :first-child {
  margin-left: 0;
}

.cepkoncept-about .content .about-footer .btn {
  height: 5rem;
  aspect-ratio: 43 / 12;
  border: .4rem solid var(--primary-accent-color);
  text-transform: capitalize;
  font-size: 1.4rem;
  font-weight: 700;
  transition: color .3s ease-in-out;
}

.cepkoncept-about .content .about-footer .btn:hover {
  color: #fff;
}

.cepkoncept-about .content .about-footer .btn:hover:before {
  width: 100%;
}

.cepkoncept-about .content .about-footer .btn:before {
  background: var(--primary-accent-color);
}

@media screen and (orientation: landscape) {
  .cepkoncept-about {
    width: 90%;
    aspect-ratio: 2.25;
    box-shadow: inherit;
    border: .3rem solid #000;
    display: flex;
  }

  .cepkoncept-about .content {
    flex-direction: column;
    flex-basis: 65%;
    justify-content: center;
    padding: 0;
    display: flex;
    position: relative;
  }

  .cepkoncept-about .content .about-header {
    text-align: center;
  }

  .cepkoncept-about .content .about-text {
    padding: 0 7rem;
  }

  .cepkoncept-about .content .about-footer {
    padding: 4rem 7rem;
  }

  .cepkoncept-about .image {
    background-image: linear-gradient(#00000040, #00000040), url("(11).fce81255.png");
    background-position: center;
    background-size: cover;
    flex-basis: 35%;
    display: block;
  }

  .cepkoncept-about .image .border {
    height: 130%;
    border-right: .3rem solid #000;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
}

.cepkoncept-footer {
  color: #fff;
  margin-top: 10rem;
  position: relative;
}

.cepkoncept-footer .crown {
  height: 7.5rem;
  border-left: .3rem solid var(--primary-accent-color);
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

.cepkoncept-footer .section-0 {
  background: #1b1b1b;
  flex-flow: column;
  justify-content: space-around;
  padding: 15rem 0;
  display: flex;
}

.cepkoncept-footer .section-0 > * {
  margin: 2rem 0;
}

.cepkoncept-footer .section-0 .socials {
  justify-content: center;
  align-items: center;
  display: flex;
}

.cepkoncept-footer .section-0 .socials .encircled-icon {
  width: 4rem;
  fill: #fff;
}

.cepkoncept-footer .section-0 .socials > :first-child {
  margin-left: 0;
}

.cepkoncept-footer .section-0 .header {
  text-transform: uppercase;
  text-align: center;
  font-size: 5rem;
}

.cepkoncept-footer .section-0 .btn {
  height: 6rem;
  max-width: 25rem;
  aspect-ratio: 43 / 12;
  border: .4rem solid var(--primary-accent-color);
  text-transform: capitalize;
  margin: 0 auto;
  font-size: 1.75rem;
  font-weight: 700;
  transition: color .3s ease-in-out;
}

.cepkoncept-footer .section-0 .btn:hover {
  color: #fff;
}

.cepkoncept-footer .section-0 .btn:hover:before {
  width: 100%;
}

.cepkoncept-footer .section-0 .btn:before {
  background: var(--primary-accent-color);
}

.cepkoncept-footer .section-1 {
  background: #161616;
  padding: 7rem 0;
  display: flex;
}

.cepkoncept-footer .section-1 .first, .cepkoncept-footer .section-1 .second {
  text-align: center;
  flex-basis: 50%;
}

.cepkoncept-footer .section-1 .first .location, .cepkoncept-footer .section-1 .second .location {
  font-variant: initial;
  text-transform: capitalize;
  font-size: 3rem;
}

.cepkoncept-footer .section-2 {
  background: #000;
  padding: 3rem 0;
}

.cepkoncept-footer .section-2 .copy {
  color: #696969;
  text-align: center;
}

@media screen and (orientation: landscape) {
  .cepkoncept-footer .section-0 {
    flex-flow: row;
  }

  .cepkoncept-footer .section-0 .btn {
    margin: 2rem 0;
  }
}

.cepkoncept-hero {
  height: 100vh;
  background-image: linear-gradient(#000000b3, #000000b3), url("(8).20a1d002.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  grid-template-columns: auto;
  padding-top: 13rem;
  display: grid;
  box-shadow: 0 .4rem .8rem #0003, 0 .6rem 2rem #00000030;
}

.cepkoncept-hero .hero-header {
  color: #fff;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  padding: 0 4rem;
  display: flex;
}

.cepkoncept-hero .hero-header .sub-header {
  padding-bottom: 1rem;
}

.cepkoncept-hero .hero-header .main-header {
  margin-left: -.4rem;
  padding: 0;
  font-size: 7rem;
}

.cepkoncept-hero .hero-header .hero-text {
  width: 100%;
  padding: 2.5rem 0 4rem;
  font-size: 1.2rem;
}

.cepkoncept-hero .hero-header .hero-text b {
  color: var(--primary-accent-color);
  text-transform: uppercase;
}

.cepkoncept-hero .hero-header .btn {
  color: #000;
  width: 20rem;
  text-transform: uppercase;
  margin: 0 auto;
  font-size: 1.75rem;
  font-weight: 900;
}

.cepkoncept-hero .hero-header .btn:before {
  width: 100%;
  background: var(--primary-accent-color);
}

.cepkoncept-hero .hero-header .btn .plus {
  height: 1.75rem;
  margin-right: 1rem;
}

.cepkoncept-hero .hero-socials {
  width: 1.25rem;
  flex-flow: column;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 1.5rem;
}

.cepkoncept-hero .hero-socials a {
  margin: 1rem 0;
}

.cepkoncept-hero .hero-socials svg {
  width: 100%;
  fill: #fff;
  transition: fill .2s;
}

.cepkoncept-hero .hero-socials svg:hover {
  fill: var(--primary-accent-color);
}

.cepkoncept-hero .hero-projects {
  display: none;
}

@media screen and (orientation: landscape) {
  .cepkoncept-hero {
    grid-template-columns: auto 35%;
  }

  .cepkoncept-hero .mobile-br {
    display: none;
  }

  .cepkoncept-hero .hero-header {
    text-align: left;
    padding: 0 14rem;
  }

  .cepkoncept-hero .hero-header .btn {
    margin: 0;
  }

  .cepkoncept-hero .hero-header .hero-text {
    width: 75%;
  }

  .cepkoncept-hero .hero-projects {
    height: 99%;
    backdrop-filter: blur(10px);
    background: #0003;
    padding: 3em 5rem;
    display: block;
    overflow-y: hidden;
  }

  .cepkoncept-hero .hero-projects .header {
    color: #fff;
    font-variant: small-caps;
    text-transform: capitalize;
    padding-bottom: 1rem;
    font-size: 3rem;
    font-weight: 700;
  }

  .cepkoncept-hero .hero-projects .project-card {
    aspect-ratio: 2.4;
    box-shadow: 1px 1px black, 3px 3px white, 5px 5px var(--primary-accent-color);
    background: #f0f0f0;
    flex: none;
    grid-template-columns: 35% auto;
    margin: 1.5rem 0;
    padding: 2rem;
    display: grid;
  }

  .cepkoncept-hero .hero-projects .project-card .details {
    padding: 1.5rem;
  }

  .cepkoncept-hero .hero-projects .project-card .image {
    align-items: center;
    display: flex;
  }

  .cepkoncept-hero .hero-projects .project-card .image img {
    width: 100%;
    aspect-ratio: 1 / 1;
  }

  .cepkoncept-hero .hero-projects .project-card .title {
    text-transform: capitalize;
    font-size: 1.5rem;
    font-weight: 700;
  }

  .cepkoncept-hero .hero-projects .project-card .date {
    color: #646464;
    padding: 1rem 0;
  }

  .cepkoncept-hero .hero-projects .project-card .date svg {
    width: 1.25rem;
    vertical-align: bottom;
    fill: #c8c8c8;
  }

  .cepkoncept-hero .hero-projects .project-card .footer {
    display: flex;
  }

  .cepkoncept-hero .hero-projects .project-card .footer .view-more {
    height: 3.5rem;
    aspect-ratio: 2.5 / 1;
    text-transform: uppercase;
    background: var(--primary-accent-color);
    margin-right: 1rem;
    font-weight: 700;
  }

  .cepkoncept-hero .hero-projects .project-card .footer .bell {
    height: 3.5rem;
    aspect-ratio: 1 / 1;
    background: #fff;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .cepkoncept-hero .hero-projects .project-card .footer .bell svg {
    height: 50%;
    fill: var(--primary-accent-color);
  }
}

.cepkoncept-navbar {
  width: 100vw;
  color: #fff;
  height: 7.5rem;
  z-index: 1000;
  border-bottom: .3rem solid #0000;
  justify-content: space-between;
  padding: 0 3vw;
  font-weight: 700;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 0;
}

.cepkoncept-navbar .logo, .cepkoncept-navbar .navbar-link {
  line-height: 7.5rem;
}

.cepkoncept-navbar .logo {
  font-variant: small-caps;
  text-transform: capitalize;
  flex-basis: 50%;
  font-size: 3rem;
}

.cepkoncept-navbar .logo:first-letter {
  color: var(--primary-accent-color);
}

.cepkoncept-navbar .sidebar-btn svg {
  fill: #fff;
  height: 2.5rem;
  margin-top: .5rem;
  transition: fill .3s;
}

.cepkoncept-navbar .sidebar-btn svg:hover {
  fill: var(--primary-accent-color);
}

.cepkoncept-navbar .navigation, .cepkoncept-navbar .square-menu {
  display: none;
}

@media screen and (orientation: landscape) {
  .cepkoncept-navbar .ellipsis-menu {
    display: none;
  }

  .cepkoncept-navbar .square-menu {
    display: inline-block;
  }

  .cepkoncept-navbar .navigation {
    flex-basis: 42.5%;
    display: block;
  }

  .cepkoncept-navbar .navigation .list {
    justify-content: space-between;
    display: flex;
  }

  .cepkoncept-navbar .navigation .navbar-link {
    text-transform: uppercase;
    padding: 0 1rem;
    font-size: 1.3rem;
    transition: color .3s;
  }

  .cepkoncept-navbar .navigation .navbar-link:hover, .cepkoncept-navbar .navigation .active {
    color: var(--primary-accent-color);
  }
}

.fixed {
  color: #000;
  border-bottom: .3rem solid var(--primary-accent-color);
  background: #fff;
  box-shadow: 0 .4rem .8rem #0003, 0 .6rem 2rem #00000030;
}

.fixed .sidebar-btn svg {
  fill: #000;
}

