.cepkoncept-services .section-header {
  text-align: center;
  padding: 5rem 0;
  position: relative;
}

.cepkoncept-services .section-header .crown {
  height: 5rem;
  border-left: .3rem solid var(--primary-accent-color);
  position: absolute;
  top: 0;
