.cepkoncept-team {
  flex-flow: column;
  display: flex;
}

.cepkoncept-team .image {
  width: 100%;
  aspect-ratio: 1.5;
}

.cepkoncept-team .image img {
  object-position: center top;
}

.cepkoncept-team .description {
  color: #fff;
  background: var(--dark-bg-0);
  flex-direction: column;
  justify-content: center;
  padding: 0 3rem;
  display: flex;
}

.cepkoncept-team .description .main-header {
  font-size: 5rem;
}

.cepkoncept-team .description .team-footer {
  justify-content: space-between;
  padding: 5rem 0;
  display: flex;
}

.cepkoncept-team .description .team-footer svg {
  width: 1.25rem;
  fill: var(--primary-accent-color);
}

@media screen and (orientation: landscape) {
  .cepkoncept-team {
    aspect-ratio: 2;
    flex-flow: row;
  }

  .cepkoncept-team .image {
    flex-basis: 40%;
  }

  .cepkoncept-team .description {
    flex-basis: 60%;
    padding: 0 7rem;
  }
}



html {
  scroll-behavior: smooth;
  font-family: Montserrat;
